body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  font-size: 16px;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  height: 100vh;
  width: 100vw;
  overflow: hidden;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

a {
  color: blue;
  text-decoration: none;
}

a:hover {
  color: skyblue;
}
.app {
    height: 100vh;
    width: 100vw;
    display: grid;
    grid-template-rows: 2.5em auto 3em;
    grid-template-columns: auto;
    grid-template-areas: 
      "header"
      "body"
      "footer";
}

.header {
    grid-area: header;
}

.body {
    grid-area: body;
    padding: 0 1em 0 1em;
    overflow: auto;
}

.caution {
    grid-area: footer;
    width: 100%;
    background-repeat: repeat;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-flow: column;
            flex-flow: column;
    -webkit-justify-content: center;
            justify-content: center;
    text-align: center;
    -webkit-align-items: center;
            align-items: center;
    font-weight: 700;
}
  
.caution p {
    margin: 0;
    padding: 2px;
    background-color: black;
    color: white;
}
  
.caution a,
.caution a:visited {
    color: skyblue;
}
  
.caution a:active,
.caution a:focus,
.caution a:hover {
    color: blue;
}

h1 {
  font-size: 1.5em;
  font-weight: 400;
  text-align: center;
}


caption {
  font-size: 1.125em;
  font-weight: 700;
}

.table__number {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
  text-align: right;
}

td {
    padding: 0 0.5em;
}

.center {
  text-align: center;
}

.inactive .table__player,
.inactive .table__number,
li.inactive {
  opacity: 0.5;
}

.dummy {
  font-style: italic;
}

textarea.json {
    font-family: 'Fira Code', Menlo, Monaco, 'Courier New', monospace;
}
